body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.button {
  color: #490c07;
  font-family: UTMKhuccamta;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  transition: 0.2s linear transform;
}

.button:active {
  transform: scale(0.9);
}
@keyframes float-right {
  0% {
    transform: translate(0px, 0px);
  }
  50% {
    transform: translate(10px, -20px);
  }
  100% {
    transform: translate(0px, 0px);
  }
}
@keyframes float-left {
  0% {
    transform: translate(0px, 0px);
  }
  50% {
    transform: translate(-10px, -20px);
  }
  100% {
    transform: translate(0px, 0px);
  }
}

.zs-progress {
  display: none !important;
}

@keyframes floating {
  25% {
    transform: translateY(7px);
  }
  50% {
    transform: translateY(0);
  }
  75% {
    transform: translateY(7px);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes bounce {
  60% {
    transform: scale(1);
  }

  70% {
    transform: scale(1.1);
  }

  80% {
    transform: scale(0.9);
  }

  90% {
    transform: scale(1.1);
  }

  100% {
    transform: scale(1);
  }
}
@-webkit-keyframes floating {
  25% {
    transform: translateY(7px);
  }
  50% {
    transform: translateY(0);
  }
  75% {
    transform: translateY(7px);
  }
  100% {
    transform: translateY(0);
  }
}
@-webkit-keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@-webkit-keyframes bounce {
  60% {
    transform: scale(1);
  }

  70% {
    transform: scale(1.1);
  }

  80% {
    transform: scale(0.9);
  }

  90% {
    transform: scale(1.1);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes swing {
  0% {
    transform: rotate(3deg);
    transform-origin: 0% 0%;
  }

  50% {
    transform: rotate(-3deg);
    transform-origin: 0% 0%;
  }

  100% {
    transform: rotate(3deg);
    transform-origin: 0% 0%;
  }
}
@-webkit-keyframes swing {
  0% {
    transform: rotate(3deg);
    transform-origin: 0% 0%;
  }

  50% {
    transform: rotate(-3deg);
    transform-origin: 0% 0%;
  }

  100% {
    transform: rotate(3deg);
    transform-origin: 0% 0%;
  }
}

html {
  background: #ed7e1f;
}

@keyframes glow {
  0% {
    box-shadow: 0 0 5px #f74104d0, 0 0 20px #f74104d0, 0 0 25px #f74104d0;
  }

  50% {
    box-shadow: 0 0 15px #ee0606, 0 0 20px #ee0606, 0 0 25px #ee0606,
      0 0 30px #ee0606;
  }
  100% {
    box-shadow: 0 0 5px #f74104d0, 0 0 20px #f74104d0, 0 0 25px #f74104d0;
  }
}
@-webkit-keyframes glow {
  0% {
    box-shadow: 0 0 5px #f74104d0, 0 0 20px #f74104d0, 0 0 25px #f74104d0;
  }

  50% {
    box-shadow: 0 0 15px #ee0606, 0 0 20px #ee0606, 0 0 25px #ee0606,
      0 0 30px #ee0606;
  }
  100% {
    box-shadow: 0 0 5px #f74104d0, 0 0 20px #f74104d0, 0 0 25px #f74104d0;
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.65);
  z-index: 4;
}
